@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
.container {
  padding-right: 50px;
  padding-left: 50px;
  margin-right: auto;
  margin-left: auto;
  width: 1280px;
}

@media (max-width: 1280px) {
  .container {
    max-width: 960px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 720px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 540px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.form {
  font-size: 14px;
}

.form__error {
  font-size: 12px;
  color: #E52D2D;
  position: absolute;
  bottom: -18px;
  left: 13px;
  right: 0;
  display: none;
}

.form__error.active {
  display: block;
}

.form__group {
  margin: 0 8px 25px;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  position: relative;
}

.form__group_inline {
  flex-direction: row;
  align-items: center;
}

.form__row {
  display: flex;
  margin: 0 -8px;
  position: relative;
  flex-wrap: wrap;
}

.form input[type=checkbox] {
  margin-right: 36px;
  width: 0px;
  height: 0px;
  position: relative;
}

.form input[type=checkbox] + label {
  cursor: pointer;
}

.form input[type=checkbox] + label::before {
  position: absolute;
  content: '';
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background: #6EBBD3;
  border-radius: 3px;
}

.form input[type=checkbox]:checked + label::before {
  background: #6EBBD3 url("../img/checked.svg") center center no-repeat;
}

.form input[type=email],
.form input[type=text] {
  padding: 22px 30px;
  font-weight: bold;
  border-radius: 10px;
  border: none;
  width: 100%;
}

.form input[type=email]:focus,
.form input[type=text]:focus {
  outline: none;
}

.btn {
  border: none;
  border-radius: 10px;
  padding: 22px 30px;
  font-weight: bold;
}

.btn:focus {
  outline: none;
}

.btn_transparent {
  background: transparent;
  font-weight: normal;
  border: 1px solid #CACACA;
}

.btn_transparent:hover {
  border: 1px solid #000000;
}

.btn_blue {
  background: #6EBBD3;
  color: #fff;
}

.btn_blue:hover {
  background: #4eb1cf;
}

.btn_blue:active {
  background: #9dd3e4;
}

.btn_black {
  background: #000000;
  color: #fff;
}

.nav {
  color: #fff;
  display: flex;
  align-items: flex-end;
  padding-top: 40px;
}

.nav__container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.nav__logo {
  flex-basis: 15%;
}

.nav__mobile-wrap {
  display: flex;
  flex-basis: 85%;
}

@media (max-width: 768px) {
  .nav__mobile-wrap {
    display: none;
  }
}

.nav__toggle {
  display: none;
  z-index: 500;
}

@media (max-width: 768px) {
  .nav__toggle {
    display: block;
  }
}

.nav__toggle-btn {
  background: url("../img/burg.svg") center center no-repeat;
  width: 18px;
  height: 18px;
  border: none;
}

.nav__menu {
  display: flex;
  align-items: center;
  flex-basis: 70%;
  margin: 0 -35px;
}

@media (max-width: 1280px) {
  .nav__menu {
    margin: 0 -25px;
  }
}

@media (max-width: 992px) {
  .nav__menu {
    margin: 0 -10px;
    flex-basis: 55%;
  }
}

.nav__right {
  flex-basis: 30%;
  text-align: right;
}

@media (max-width: 992px) {
  .nav__right {
    flex-basis: 45%;
  }
}

.nav__link {
  margin: 0 35px;
}

@media (max-width: 1280px) {
  .nav__link {
    margin: 0 25px;
  }
}

@media (max-width: 992px) {
  .nav__link {
    margin: 0 10px;
  }
}

.nav__link a:hover {
  color: #4eb1cf;
}

.nav__link a:active {
  color: #9dd3e4;
}

.nav__phone {
  font-size: 24px;
  font-weight: bold;
}

@media (max-width: 992px) {
  .nav__phone {
    font-size: 20px;
  }
}

@media (max-width: 768px) {
  .nav.active .nav__mobile-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 0;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: #000000;
  }
}

@media (max-width: 768px) {
  .nav.active .nav__menu {
    flex-direction: column;
    margin-bottom: 50px;
    flex-basis: auto;
  }
}

@media (max-width: 768px) {
  .nav.active .nav__link:not(:last-child) {
    margin-bottom: 25px;
  }
}

.nav.active .nav__toggle-btn {
  background: url("../img/close.svg") center center no-repeat;
}

.header {
  background: #000 url("../img/header.png") center center no-repeat;
  background-size: cover;
  position: relative;
}

.header::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(31, 32, 33, 0.79);
  z-index: 100;
}

.header > * {
  z-index: 150;
  position: relative;
}

.header__nav {
  z-index: 200;
}

.header__h {
  height: 162px;
  display: flex;
  align-items: center;
  color: #fff;
}

@media (max-width: 576px) {
  .header__h .h1 {
    font-size: 22px;
  }
}

.sort {
  display: flex;
  flex-wrap: wrap;
  margin: 35px 0;
}

.sort__age, .sort__price {
  margin-right: 50px;
}

@media (max-width: 768px) {
  .sort__age, .sort__price {
    margin-right: 0;
    min-width: calc(50%);
  }
}

.sort__title {
  margin-right: 50px;
  font-weight: bold;
}

@media (max-width: 768px) {
  .sort__title {
    min-width: 100%;
    margin-bottom: 10px;
  }
}

.sort__btn {
  background: none;
  border: none;
  font-size: 18px;
  font-weight: normal;
  position: relative;
  padding-right: 30px;
}

.sort__btn::before {
  content: '';
  position: absolute;
  top: 3px;
  right: 0;
  width: 15px;
  height: 15px;
  background: url("../img/arrow-down.svg") center center no-repeat;
  transition: all .3s;
}

.sort__btn.rotate::before {
  transform: rotate(180deg);
}

.cards {
  margin-bottom: 80px;
  position: relative;
}

.cards__content {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  margin-bottom: 70px;
}

.cards__item {
  flex: 1 1 calc(100% / 3 - 20px);
  max-width: calc(100% / 3 - 20px);
  margin: 0 10px;
}

.cards__item:nth-last-of-type(n+4) {
  margin-bottom: 25px;
}

@media (max-width: 992px) {
  .cards__item {
    flex: 1 1 calc(100% / 2 - 20px);
    max-width: calc(100% / 2 - 20px);
  }
  .cards__item:nth-last-of-type(n+3) {
    margin-bottom: 25px;
  }
}

@media (max-width: 768px) {
  .cards__item {
    flex: 1 1 calc(100% / 1 - 20px);
    max-width: calc(100% / 1 - 20px);
  }
  .cards__item:nth-last-of-type(n+2) {
    margin-bottom: 25px;
  }
}

.cards__more {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -25px;
}

.cards__more button {
  flex: 1 1 calc(100% / 3 - 20px);
  max-width: calc(100% / 3 - 20px);
  margin: 0 auto;
  font-size: 14px;
}

@media (max-width: 992px) {
  .cards__more button {
    flex: 1 1 calc(100% / 2 - 20px);
    max-width: calc(100% / 2 - 20px);
  }
}

.card {
  position: relative;
}

.card__sale {
  position: absolute;
  top: 30px;
  left: 30px;
  padding: 10px 20px;
  background: #E52D2D;
  color: #fff;
  font-size: 16px;
  border-radius: 10px;
  cursor: default;
}

@media (max-width: 1280px) {
  .card__sale {
    top: 15px;
    left: 15px;
    padding: 5px 10px;
    font-size: 14px;
  }
}

.card__like {
  transition: all .3s;
  position: absolute;
  background: url("../img/like.svg") center center no-repeat;
  top: 30px;
  right: 30px;
  width: 46px;
  height: 46px;
  cursor: pointer;
  opacity: .5;
  z-index: 50;
}

@media (max-width: 1280px) {
  .card__like {
    top: 15px;
    right: 15px;
    width: 30px;
    height: 30px;
    background-size: contain;
  }
}

.card__like.true {
  opacity: 1;
  animation: vanishIn .3s ease 0s 1 normal both;
}

.card__like.false {
  animation: vanishOut .3s ease 0s 1 normal backwards;
}

.card.saled .card__btn {
  cursor: not-allowed;
}

.card__img img {
  width: 100%;
  height: auto;
}

.card__desc {
  padding: 30px;
  background: #F3F3F3;
}

@media (max-width: 1280px) {
  .card__desc {
    padding: 15px;
  }
}

.card__name {
  margin-bottom: 25px;
}

.card__info {
  display: flex;
  font-size: 12px;
  margin-bottom: 25px;
  padding-left: 15px;
  position: relative;
}

.card__info::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 4px;
  background: #6EBBD3;
}

.card__age, .card__color, .card__paws {
  flex: 1 1 calc(100% / 3);
  min-width: calc(100% / 3);
}

@media (max-width: 1280px) {
  .card .card__color {
    flex: 1 1 40%;
    min-width: 40%;
  }
  .card .card__age,
  .card .card__paws {
    flex: 1 1 30%;
    min-width: 30%;
  }
}

.card__age-val, .card__paws-val {
  font-weight: bold;
}

.card__price {
  font-size: 24px;
  font-weight: bold;
}

.card__btn {
  width: 100%;
  font-size: 16px;
  border-radius: 0 0 10px 10px;
}

@keyframes vanishOut {
  0% {
    opacity: 1;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0);
    filter: blur(0);
  }
  100% {
    opacity: 0;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(20px);
    filter: blur(20px);
  }
}

@keyframes vanishIn {
  0% {
    opacity: 0;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(90px);
    filter: blur(90px);
  }
  100% {
    opacity: 1;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0);
    filter: blur(0);
  }
}

.footer {
  background: #000000;
  padding: 80px 80px 90px;
  color: #fff;
  display: flex;
}

@media (max-width: 992px) {
  .footer {
    padding: 40px 40px 50px;
  }
}

@media (max-width: 768px) {
  .footer {
    flex-direction: column;
  }
}

.footer__right {
  flex: 1 1 calc(55% - 10px);
  min-width: calc(55% - 10px);
}

.footer__left {
  flex: 1 1 calc(45% - 10px);
  min-width: calc(45% - 10px);
  margin-right: 20px;
}

@media (max-width: 768px) {
  .footer__left {
    margin-bottom: 30px;
  }
}

.footer__desc {
  font-size: 28px;
  line-height: 90%;
  max-width: 400px;
}

@media (max-width: 992px) {
  .footer__desc {
    font-size: 22px;
  }
}

.footer__title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 25px;
}

@media (max-width: 992px) {
  .footer__title {
    font-size: 40px;
    line-height: 36px;
    margin-bottom: 15px;
  }
}

.scroll-top {
  content: '';
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 1000;
  opacity: .5;
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease;
}

.scroll-top.active {
  visibility: visible;
  opacity: 1;
}

@media (max-width: 768px) {
  .scroll-top {
    right: 10px;
    bottom: 10px;
  }
}

.scroll-top:hover {
  opacity: 1;
}

.scroll-top.btn {
  border-radius: 0;
  padding: 0;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 18px;
  scroll-behavior: smooth;
  font-family: 'Montserrat', sans-serif;
}

img {
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

ul li {
  list-style: none;
}

.h3 {
  font-size: 24px;
  font-weight: bold;
}

button {
  cursor: pointer;
}
